/* eslint-disable react/display-name */
//https://github.com/tannerlinsley/react-table/

import { styled, theme } from "@faharmony/theme";

interface StyleProps {
  handleRowClick: any;
  handleRowDblClick: any;
}

export const Styles = styled.div<StyleProps>`
  /* This is required to make the table full-width */
  display: block;
  max-width: 100%;
  width: 100%;
  height: 100%;
  min-height: 120px;
  background-color: ${theme.color.background.default};

  table {
    table-layout: fixed;
    width: auto;
    min-width: 100%;
    border-spacing: 0;
    position: relative;

    thead {
      tr {
        min-height: 40px;

        th {
          position: sticky;
          top: 0;
          background-color: ${theme.color.background.disabled};
          border-bottom: 1px solid ${theme.color.border.default};
          border-right: 1px solid ${theme.color.border.default};
          min-height: 40px;
          z-index: 200;

          margin: 0;
          text-align: left;
          padding: ${theme.spacing.md};

          :last-child {
            border-right: 0;
          }

          &.fixedColumnStart,
          &.fixedColumnEnd {
            position: sticky;
            z-index: 250;
          }
          &.fixedColumnStart {
            left: 0;
          }
          &.fixedColumnEnd {
            right: 0;
          }

          .table-header-sort {
            /* position: absolute;
            right: ${theme.spacing.sm};
            top: ${theme.spacing.md}; */
            color: ${theme.color.text.disabled};
            opacity: 0.5;
            cursor: pointer;

            :hover {
              opacity: 1;
            }
          }
        }
      }

      .filterRow {
        th {
          position: sticky;
          top: 40px;
          padding: ${theme.spacing.sm};
          background-color: ${theme.color.background.disabled};
          z-index: 250;

          input,
          button,
          select {
            font-family: inherit;
            color: ${theme.color.text.primary};
            background-color: ${theme.color.background.default};
            border-radius: ${theme.radius[100]};
            height: 28px;
            border: 1px solid ${theme.color.border.default};
            padding: 0 4px;
          }

          button {
            width: 28px;
          }
        }
      }
    }

    tbody tr {
      position: relative;
      z-index: 100;
      cursor: ${(p) =>
        p.handleRowClick || p.handleRowDblClick ? "pointer" : "initial"};

      td {
        margin: 0;
        text-align: left;
        padding: ${theme.spacing.md};
        background-color: ${theme.color.background.default};

        :last-child {
          border-right: 0;
        }

        &.fixedColumnStart,
        &.fixedColumnEnd {
          position: sticky;
          z-index: 150;
        }
        &.fixedColumnStart {
          left: 0;
        }
        &.fixedColumnEnd {
          right: 0;
        }
      }

      &:nth-of-type(odd),
      &:nth-of-type(odd) td {
        background-color: ${theme.color.background.rowAlt};
      }

      &:last-child {
        td {
          border-bottom: 0;
        }
      }

      &.selected,
      &.selected td {
        background-color: ${theme.color.background.selected} !important;
      }

      &:hover,
      &:hover td {
        background-color: ${theme.color.background.hover} !important;
      }

      &.subComponent td {
        padding: 0;
        margin: 0;
        background-color: ${theme.color.background.primary} !important;
      }
    }

    tfoot {
      position: sticky;
      z-index: 200;
      bottom: 0;
      background-color: ${theme.color.background.default};
      z-index: 200;

      tr:first-of-type {
        td {
          border-top: 1px solid ${theme.color.border.default};
          border-right: 1px solid ${theme.color.border.default};
          :last-child {
            border-right: 0;
          }
        }
      }
    }
  }
`;

export default Styles;
