import React from "react";
import { Row } from "react-table";
import { faCaretRight, faCaretDown } from "@faharmony/icons/solid";
import { uuid } from "@faharmony/helpers";
import { theme, Grid } from "@faharmony/theme";
import { Icon } from "@faharmony/components";

import { TableChildrenProps } from "../types";

export const TableBody = <T extends Record<string, any>>({
  tableInstance,
  tableProps,
}: TableChildrenProps<T>) => {
  const {
    getTableBodyProps,
    rows,
    page,
    prepareRow,
    toggleAllRowsSelected,
    toggleRowSelected,
    toggleRowExpanded,
    visibleColumns,
  } = tableInstance;
  const {
    isLoading,
    handleRowDblClick,
    handleRowClick,
    handleRowContextClick,
    enablePagination,
    enableMultipleRowSelect,
    data,
    renderRowSubComponent,
  } = tableProps;

  const visibleRows = enablePagination ? page : rows;
  const enableExpand =
    data.filter((row: any) => (row.subRows ? true : false)).length > 0 ||
    renderRowSubComponent;

  // HANDLE CLICK
  const timeWaitForDoubleClick = 400;
  let clickCount = 0;
  let singleClickTimer: NodeJS.Timeout;
  function handleRowClickAndDblClick(row: Row<T>) {
    clickCount++;
    if (clickCount === 1) {
      singleClickTimer = setTimeout(() => {
        clickCount = 0;
        // Selected Row Effect
        if (!enableMultipleRowSelect) {
          toggleAllRowsSelected(false);
          toggleRowSelected(row.id);
        }
        // Execute Single Click
        if (handleRowClick) handleRowClick(row.original);
      }, timeWaitForDoubleClick);
    } else if (clickCount === 2) {
      clearTimeout(singleClickTimer);
      clickCount = 0;
      // Execute Double Click
      if (handleRowDblClick) handleRowDblClick(row.original);
    }
  }

  return (
    <tbody {...getTableBodyProps()}>
      {visibleRows.map((row) => {
        prepareRow(row);
        return (
          <React.Fragment key={row.id || uuid()}>
            <tr
              {...row.getRowProps()}
              onClick={() => !isLoading && handleRowClickAndDblClick(row)}
              onContextMenu={(e) => {
                if (!isLoading && handleRowContextClick) {
                  e.preventDefault();
                  e.stopPropagation();
                  handleRowContextClick(row.original);
                }
              }}
              className={row.isSelected ? "selected" : ""}
            >
              {row.cells.map((cell, index) => {
                return (
                  <td
                    {...cell.getCellProps()}
                    style={{
                      ...cell.getCellProps().style,
                      minWidth: cell.column.minWidth,
                      maxWidth: cell.column.maxWidth,
                    }}
                  >
                    {enableExpand &&
                    ((enableMultipleRowSelect && index === 1) ||
                      (!enableMultipleRowSelect && index === 0)) ? (
                      // First column with content
                      row.canExpand || renderRowSubComponent ? (
                        // Row which can expand
                        <Grid
                          gridTemplate="auto / max-content auto"
                          style={{
                            paddingLeft: `calc(${row.depth} * ${theme.spacing.xl})`,
                          }}
                        >
                          <span
                            style={{ cursor: "pointer" }}
                            onClick={(e) => {
                              e.stopPropagation();
                              toggleRowExpanded([row.id], !row.isExpanded);
                            }}
                          >
                            <Icon
                              icon={row.isExpanded ? faCaretDown : faCaretRight}
                            />
                          </span>
                          <span style={{ paddingTop: theme.spacing.xs }}>
                            {cell.render("Cell")}
                          </span>
                        </Grid>
                      ) : (
                        // Row which cannot expand further
                        <div
                          style={{
                            paddingLeft: `calc(${row.depth + 2} * ${
                              theme.spacing.xl
                            })`,
                          }}
                        >
                          {cell.render("Cell")}
                        </div>
                      )
                    ) : (
                      // Normal cell
                      <div
                        style={{
                          textAlign:
                            index === (enableMultipleRowSelect ? 1 : 0)
                              ? undefined
                              : cell.column.align,
                        }}
                      >
                        {cell.render("Cell")}
                      </div>
                    )}
                  </td>
                );
              })}
            </tr>
            {/* SubComponent */}
            {renderRowSubComponent && !row.canExpand && row.isExpanded && (
              <tr key="subComponent" className="subComponent">
                <td colSpan={visibleColumns.length}>
                  {renderRowSubComponent(row.original)}
                </td>
              </tr>
            )}
          </React.Fragment>
        );
      })}
    </tbody>
  );
};

export default TableBody;
