import React from "react";
import { Icon } from "@faharmony/components";
import { faSquare } from "@faharmony/icons";
import { faCheckSquare, faMinusSquare } from "@faharmony/icons/solid";
import { CSSProperties } from "@faharmony/theme";

interface ICheckedIconProps {
  onChange?: (e: React.MouseEvent<HTMLElement, MouseEvent>) => void;
  checked?: boolean;
  title?: string;
  indeterminate?: boolean;
  style?: CSSProperties;
}

export const CheckedIcon = ({
  onChange,
  checked,
  indeterminate,
  title,
  style,
}: ICheckedIconProps) => (
  <div
    onClick={(e) => {
      e.preventDefault();
      e.stopPropagation();
      onChange && onChange(e);
    }}
    title={title}
    style={style as any}
  >
    <Icon
      icon={indeterminate ? faMinusSquare : checked ? faCheckSquare : faSquare}
    />
  </div>
);

export default CheckedIcon;
