//https://github.com/tannerlinsley/react-table/

import React from "react";
import { theme } from "@faharmony/theme";
import { ButtonPopover, Text, Divider } from "@faharmony/components";

import { TableChildrenProps } from "../types";
import { commonButtonOptions } from "./common";

export const rowActions = <T extends object>({
  tableInstance,
  tableProps,
}: TableChildrenProps<T>) => {
  const { selectedFlatRows = [], state } = tableInstance;
  const { selectedRowIds } = state;
  const {
    multipleRowActions,
    singleRowActions,
    enableMultipleRowSelect: selectMultipleRows,
  } = tableProps;

  const selectedRowIdsList = Object.entries(selectedRowIds);
  const isRowSelected = selectedRowIdsList.length > 0;
  const selectedRows = selectedFlatRows.map((row) => row.original);

  // If multipleRows enabled, show multipleRowActions, else singleRowActions
  const rowActionList = selectMultipleRows
    ? (multipleRowActions && multipleRowActions(selectedRows)) || []
    : (singleRowActions && singleRowActions(selectedRows[0])) || [];

  const actions = [];
  if (rowActionList.length > 0) {
    isRowSelected &&
      selectMultipleRows &&
      actions.push(
        <div key="rowSelectedText" style={{ paddingTop: theme.spacing.sm }}>
          <Text
            value={`${selectedRowIdsList.length} selected`}
            color="disabled"
          />
        </div>
      );

    rowActionList.map((action) =>
      actions.push(
        <ButtonPopover
          key={action.id}
          {...action}
          {...commonButtonOptions}
          disabled={!isRowSelected}
          variant={isRowSelected ? action.variant : "default"}
        />
      )
    );

    actions.push(<Divider key={"actionDivider"} vertical />);
  }
  return actions;
};

export default rowActions;
