import React from "react";
import { faEllipsisHAlt } from "@faharmony/icons";
import { Row } from "react-table";
import { ButtonPopover } from "@faharmony/components";

import { ITableProps } from "../types";

const SingleRowActionMenu = <T extends object>({
  row: { original: rowObject },
  tableProps: { singleRowActions },
}: {
  row: Row<T>;
  tableProps: ITableProps<T>;
}) => {
  const singleRowActionList =
    (singleRowActions && singleRowActions(rowObject)) || [];

  return (
    <ButtonPopover
      icon={faEllipsisHAlt}
      spacing="xs"
      popoverMenuItems={singleRowActionList.map((action) => ({
        id: action.id,
        label: action.tooltip || "",
        icon: action.icon,
        onClick: () => action.onClick,
        tint: action.variant,
      }))}
    />
  );
};

export default SingleRowActionMenu;
