import React from "react";
import { TableInstance, Hooks, ColumnInstance, Row } from "react-table";
import { unparse } from "papaparse";

import { systemColumnsIds } from "../plugins/addColumns";
import { getFirstDefined } from "./utils";

type HeaderName = string | number | object | undefined;

type FileType = "csv";

interface ExportFile {
  data: { fields: HeaderName[]; data: any[][] };
  fileName: string;
  type: string;
}

export interface UseExportDataColumnProps<T extends object> {
  canExport: boolean;
  disableExport: boolean;
  getExportHeaderValue: (col: ColumnInstance<T>) => HeaderName;
  getExportCellValue: (
    row: Row<T>,
    col: { id: string; name: HeaderName; columnObj: ColumnInstance<T> }
  ) => any;
}

export interface UseExportDataInstanceProps {
  exportData: ({
    all,
    type,
    fileName,
  }: {
    all?: boolean;
    type?: FileType;
    fileName?: string;
  }) => void;
}

// Get exported file name(do not specify extension here)
const defaultGetExportFileName = ({
  all,
}: {
  fileType: string;
  all: boolean;
}): string => {
  return `${all ? "all-" : ""}data`;
};

// To get column name while exporting
const defaultGetExportHeaderValue = <T extends object>(
  col: ColumnInstance<T>
) => {
  let name = col.Header;
  if (typeof name === "object" || typeof name === "function") {
    name = col.id;
  }
  return name;
};

// To get cell value while exporting
const defaultGetExportCellValue = <T extends object>(
  row: Row<T>,
  col: { id: string; name: HeaderName; columnObj: ColumnInstance<T> }
) => {
  return row.values[col.id];
};

function getCsvBlob(data: ExportFile["data"]) {
  const csvString = unparse(data);
  return new Blob([csvString], { type: "text/csv" });
}

function downloadFileViaBlob(fileBlob: Blob, fileName: string, type: string) {
  if (fileBlob) {
    const dataUrl = URL.createObjectURL(fileBlob);
    const link = document.createElement("a");
    link.download = `${fileName}.${type}`;
    link.href = dataUrl;
    link.click();
  }
}

function exportFile({ data, fileName, type }: ExportFile) {
  let fileBlob = null;

  if (type === "csv") {
    fileBlob = getCsvBlob(data);
  } else if (type === "xlsx") {
    // Todo
  } else if (type === "pdf") {
    // Todo
  }
  // keep on adding support for different file types here
  else {
    console.warn("Not Supported File Type");
  }

  fileBlob && downloadFileViaBlob(fileBlob, fileName, type);
}

function useInstance<D extends object>(instance: TableInstance<D>) {
  const {
    rows,
    initialRows,
    allColumns,
    disableExport,
    getExportFileName = defaultGetExportFileName,
  } = instance;

  // Adding `canExport` meta data
  allColumns.forEach((column) => {
    const canExport = column.id
      ? getFirstDefined(
          column.disableExport === true ? false : undefined,
          disableExport === true ? false : undefined,
          true
        )
      : false;

    column.canExport = canExport;
  });

  // This method will enable export of data on `instance` object
  const exportData = React.useCallback(
    ({ all = false, type: fileType = "csv", fileName = "" } = {}) => {
      const colHeader = allColumns
        .filter(
          (col) =>
            col.canExport &&
            (all || col.isVisible) &&
            !systemColumnsIds.includes(col.id)
        )
        .map((col) => {
          const { getExportHeaderValue = defaultGetExportHeaderValue } = col;
          return {
            id: col.id,
            name: getExportHeaderValue(col),
            columnObj: col,
          };
        });

      if (colHeader.length === 0) {
        console.warn("No exportable columns are available");
      }

      let exportableRows = rows;
      if (all) {
        exportableRows = initialRows;
      }

      const data = exportableRows.map((row) => {
        return colHeader.map((col) => {
          const {
            getExportCellValue = defaultGetExportCellValue,
          } = col.columnObj;

          return getExportCellValue(row, col);
        });
      });

      const headerNames = colHeader.map((col) => col.name);

      const exportData = { fields: headerNames, data };

      const name: string =
        fileName !== "" ? fileName : getExportFileName({ fileType, all });

      exportFile({ data: exportData, fileName: name, type: fileType });
    },
    [getExportFileName, initialRows, rows, allColumns]
  );

  Object.assign(instance, {
    exportData,
  });
}

/**
 * @author Rohit Garg <> (https://github.com/gargroh)
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 */
export const useExportData = <D extends object = {}>(hooks: Hooks<D>) => {
  hooks.useInstance.push(useInstance);
};

useExportData.pluginName = "useExportData";

export default useExportData;
