import React, { useRef } from "react";
import { styled, theme, Box } from "@faharmony/theme";
import {
  faAngleDoubleLeft,
  faAngleLeft,
  faAngleRight,
  faAngleDoubleRight,
} from "@faharmony/icons";
import { Text, Button } from "@faharmony/components";
import { NumberField } from "@faharmony/form";
import { useElementDimensions } from "@faharmony/helpers";
import { TableChildrenProps } from "../types";

const calcShowingRows = (
  pageSize: number,
  pageIndex: number,
  totalRows: number
) => {
  const start = pageIndex * pageSize + 1;
  const end = pageIndex * pageSize + pageSize;
  return `${start} - ${end > totalRows ? totalRows : end}`;
};

export const TablePagination = <T extends object>({
  tableInstance,
  tableProps,
}: TableChildrenProps<T>) => {
  const {
    canPreviousPage,
    canNextPage,
    pageOptions,
    pageCount,
    gotoPage,
    nextPage,
    previousPage,
    rows,
    state,
  } = tableInstance;
  const { pageIndex, pageSize } = state;

  const { isLoading } = tableProps;

  const targetRef = useRef<HTMLDivElement>(null);
  const { width: elWidth } = useElementDimensions(targetRef);
  const wideEnough = elWidth > 550;

  const StyledPagination = styled.div`
    display: ${wideEnough ? "grid" : "block"};
    background-color: ${theme.color.background.default};
    grid-template-columns: auto max-content;
    justify-content: space-between;

    .InputField.inputContainer {
      width: 140px;
    }
  `;

  return (
    <StyledPagination className="pagination" ref={targetRef}>
      {wideEnough && (
        <Box style={{ paddingTop: theme.spacing.sm }}>
          {isLoading ? (
            <Text value={`Loading...`} color="disabled" />
          ) : rows.length > 0 ? (
            <Text
              value={`Showing ${calcShowingRows(
                pageSize,
                pageIndex,
                rows.length
              )} of ${rows.length} entries`}
              color="secondary"
            />
          ) : (
            <Text value={`There are 0 entries`} color="disabled" />
          )}
        </Box>
      )}
      <Box justifyContent={wideEnough ? "flex-end" : "space-around"}>
        <Box justifyContent="flex-start" width="auto">
          <Button
            icon={faAngleDoubleLeft}
            onClick={() => gotoPage(0)}
            disabled={!canPreviousPage}
            spacing="sm"
            outline
          />
          <Button
            icon={faAngleLeft}
            onClick={() => previousPage()}
            disabled={!canPreviousPage}
            spacing="sm"
            outline
          />
        </Box>
        <NumberField
          min={1}
          disabled={!canNextPage && !canPreviousPage}
          spacing="sm"
          value={pageIndex + 1}
          max={pageOptions.length}
          prefixText="Page"
          suffixText={"of " + pageOptions.length.toString()}
          defaultValue={1}
          precision={0}
          onChange={(value) => {
            const page = value ? Number(value) - 1 : 0;
            gotoPage(page);
          }}
        />
        <Box justifyContent="flex-end" width="auto">
          <Button
            icon={faAngleRight}
            onClick={() => nextPage()}
            disabled={!canNextPage}
            spacing="sm"
            outline
          />
          <Button
            icon={faAngleDoubleRight}
            onClick={() => gotoPage(pageCount - 1)}
            disabled={!canNextPage}
            spacing="sm"
            outline
          />
        </Box>
      </Box>
    </StyledPagination>
  );
};

export default TablePagination;
