import React from "react";
import { Column, Hooks } from "react-table";
import ITableProps from "../types";
import SingleRowActionMenu from "../components/SingleRowActionMenu";
import CheckedIcon from "../components/CheckedIcon";

export const systemColumnsIds = ["rowActions", "selection", "expander"];

export const addColumns = <T extends object>(
  hooks: Hooks<T>,
  props: ITableProps<T>
) => {
  // Change function on `getToggleAllRowsSelectedProps` if disableSubRowSelect={true}
  if (props.disableSubRowSelect)
    hooks.getToggleAllRowsSelectedProps = [
      (props, { instance }) => [
        props,
        {
          onChange: () => {
            instance.rows.forEach((row) =>
              row.toggleRowSelected(
                !instance.rows.every((row) => row.isSelected)
              )
            );
          },
          checked:
            instance.rows.length > 0 &&
            instance.rows.every((row) => row.isSelected),
          title: "Toggle All Rows Selected",
          indeterminate: Boolean(
            !instance.isAllRowsSelected &&
              Object.keys(instance.state.selectedRowIds).length
          ),
        },
      ],
    ];

  hooks.visibleColumns.push((columns) => {
    const startColumns: Column<T>[] = [];
    const endColumns: Column<T>[] = [];
    // Add row to show checkbox for multi-row-select
    props.enableMultipleRowSelect &&
      startColumns.push({
        id: "selection",
        width: 40,
        disableSortBy: true,
        disableFilters: true,
        className: "fixedColumnStart",
        // eslint-disable-next-line react/display-name
        Header: (row) => (
          <CheckedIcon {...row.getToggleAllRowsSelectedProps()} />
        ),
        // eslint-disable-next-line react/display-name
        Cell: ({ row }: any) => {
          return props.disableSubRowSelect && row.depth > 0 ? null : (
            <CheckedIcon {...row.getToggleRowSelectedProps()} />
          );
        },
      });

    // Add column to show actions dropdown
    props.singleRowActions &&
      endColumns.push({
        id: "rowActions",
        width: 40,
        disableSortBy: true,
        disableFilters: true,
        className: "fixedColumnEnd",
        // eslint-disable-next-line react/display-name
        Cell: ({ row }: any) => (
          <SingleRowActionMenu row={row} tableProps={props} />
        ),
      });

    return [...startColumns, ...columns, ...endColumns];
  });
};

export default addColumns;
