// Using React-table by Tanner Linsley - https://github.com/tannerlinsley/react-table/

import React from "react";
import {
  useTable,
  useSortBy,
  useFilters,
  usePagination,
  useRowSelect,
  useExpanded,
  ColumnInterface,
  IdType,
} from "react-table";

import { uuid } from "@faharmony/helpers";
import { theme, Box } from "@faharmony/theme";
import { Skeleton } from "@faharmony/components";
import { ContentView } from "@faharmony/views";

import Styles from "./style";
import ITableProps, { IColumnProps } from "./types";
import useExportData from "./plugins/useExportData";
import addColumns from "./plugins/addColumns";

import UseTableActions from "./actions";
import TableHead from "./table/TableHead";
import TableBody from "./table/TableBody";
import TableFoot from "./table/TableFoot";
import TablePagination from "./table/TablePagination";

import * as Filters from "./components/filters";

/**
 * Table
 * ---
 * @author Siddhant Gupta <siddhant@fasolutions.com>
 *
 * @example
 * <Table<RowProp> data={[]} columns={[]} />;
 */
const Table = <T extends object>(props: ITableProps<T>) => {
  const {
    columns,
    data,
    enableMultipleRowSelect = false,
    defaultRowsOnPage = 100,
    enablePagination = false,
    tableHeading = "",
    borderless = false,
    hideHeader = false,
    disableSubRowSelect = false,
    isLoading = false,
  } = props;

  // Loading
  const tableData = React.useMemo(
    () => (isLoading ? Array(30).fill({} as T) : data),
    [isLoading, data]
  );

  // Loading
  const tableColumns = React.useMemo(
    () =>
      isLoading
        ? columns.map((column) => ({
            ...column,
            Cell: <Skeleton height="20px" />,
          }))
        : columns,
    [isLoading, columns]
  );

  const defaultColumn: ColumnInterface<T> = React.useMemo(
    () => ({
      Filter: Filters.DefaultColumnFilter,
    }),
    []
  );

  const hiddenColumns: IdType<T>[] = React.useMemo(
    () =>
      columns
        .filter((column) => column.hidden === true)
        .map((column) => column.id || ""),
    [columns]
  );

  const tableInstance = useTable<T>(
    {
      columns: tableColumns,
      data: tableData,
      defaultColumn,
      selectMultipleRows: enableMultipleRowSelect,
      selectSubRows: !disableSubRowSelect,
      initialState: {
        pageIndex: 0,
        pageSize: defaultRowsOnPage,
        hiddenColumns,
      },
      getSubRows: (row: any) => row.subRows,
    },
    useFilters,
    useSortBy,
    useExpanded,
    usePagination,
    useRowSelect,
    useExportData,
    (hooks) => addColumns<T>(hooks, props)
  );

  React.useEffect(() => {
    tableInstance.toggleHideColumn("selection", !enableMultipleRowSelect);
  }, [enableMultipleRowSelect]);

  const { actions, enableFilter } = UseTableActions<T>({
    tableProps: props,
    tableInstance,
  });

  const [tableKey] = React.useState(uuid());

  const table = (
    <Styles
      style={{ ...props.customStyle }}
      id={tableKey}
      className="Table"
      handleRowClick={props.handleRowClick}
      handleRowDblClick={props.handleRowDblClick}
    >
      <table {...tableInstance.getTableProps()}>
        <TableHead<T>
          tableInstance={tableInstance}
          tableProps={props}
          enableFilter={enableFilter}
        />
        <TableBody<T> tableInstance={tableInstance} tableProps={props} />
        <TableFoot<T> tableInstance={tableInstance} tableProps={props} />
      </table>
    </Styles>
  );

  if (borderless) return table;

  return (
    <ContentView
      headerTitle={tableHeading || ""}
      headerHidden={hideHeader}
      headerActions={
        <Box style={{ paddingTop: theme.spacing.xs }}>{actions}</Box>
      }
      footerContent={
        enablePagination && (
          <TablePagination<T>
            tableInstance={tableInstance}
            tableProps={props}
          />
        )
      }
      overflow={isLoading ? "hidden" : "auto"}
    >
      {table}
    </ContentView>
  );
};

export { Table, Filters as ColumnFilters, ITableProps, IColumnProps };
