import React from "react";
import { uuid } from "@faharmony/helpers";
import { Column } from "react-table";
import { TableChildrenProps } from "../types";
import { theme } from "@faharmony/theme";

const isFooterEmpty = <T extends object>(columns: Column<T>[]): boolean => {
  let empty = true;
  columns.forEach((column) => {
    if (column.Footer !== undefined) empty = false;
  });
  return empty;
};

export const TableFooter = <T extends object>({
  tableInstance,
  tableProps,
}: TableChildrenProps<T>) => {
  const { columns, enableMultipleRowSelect } = tableProps;
  const { footerGroups } = tableInstance;

  if (isFooterEmpty<T>(columns)) return null;

  return (
    <tfoot key={uuid()}>
      {footerGroups.map((group) => (
        <tr {...group.getFooterGroupProps()} key={uuid()}>
          {group.headers.map((column, index) => {
            return (
              <td {...column.getFooterProps()} key={uuid()}>
                <div
                  style={{
                    textAlign:
                      index === (enableMultipleRowSelect ? 1 : 0)
                        ? undefined
                        : column.align,
                    padding: theme.spacing.md,
                  }}
                >
                  {column.render("Footer")}
                </div>
              </td>
            );
          })}
        </tr>
      ))}
    </tfoot>
  );
};

export default TableFooter;
