//https://github.com/tannerlinsley/react-table/

import React, { useMemo } from "react";
import { faColumns } from "@faharmony/icons";
import { theme } from "@faharmony/theme";
import { ContentView } from "@faharmony/views";
import { Button, useModal } from "@faharmony/components";
import { TransferShuttle } from "@faharmony/form";

interface ItemProps {
  id: string;
  value: string;
}

import { TableChildrenProps } from "../types";
import { systemColumnsIds } from "../plugins/addColumns";

import { commonButtonOptions } from "./common";

export const useColumnSelectAction = <T extends object>({
  tableInstance,
}: TableChildrenProps<T>) => {
  const { state, toggleHideColumn, visibleColumns, allColumns } = tableInstance;
  const { hiddenColumns } = state;
  const [ModalWrapper, openModal, closeModal] = useModal();

  const hiddenCols: ItemProps[] = useMemo(
    () =>
      hiddenColumns
        ? allColumns
            .filter(
              (column) =>
                hiddenColumns.some((hid) => hid === column.id) &&
                !systemColumnsIds.includes(column.id)
            )
            .map((column) => ({
              id: column.id.toString(),
              value: column.Header?.toString() || "",
            }))
        : [],
    [allColumns, hiddenColumns]
  );

  const shownCols: ItemProps[] = useMemo(
    () =>
      visibleColumns
        ? visibleColumns
            .filter((column) => !systemColumnsIds.includes(column.id))
            .map((column) => ({
              id: column.id.toString(),
              value: column.Header?.toString() || "",
            }))
        : [],
    [visibleColumns]
  );

  const hideColumns = (items: ItemProps[]) => {
    items.forEach((item) => toggleHideColumn(item.id, true));
  };
  const showColumns = (items: ItemProps[]) => {
    items.forEach((item) => toggleHideColumn(item.id, false));
  };

  return (
    <div key={"columnSelectAction"}>
      <Button
        icon={faColumns}
        tooltip={"Select visible columns"}
        {...commonButtonOptions}
        onClick={openModal}
      />
      <ModalWrapper>
        <ContentView
          headerTitle="Select columns"
          headerActions={<Button.Close onClick={closeModal} />}
        >
          <div style={{ padding: theme.spacing.md, height: "360px" }}>
            <TransferShuttle
              shuttle1={{
                heading: "Available columns",
                items: hiddenCols,
                handleAcceptTransfer: hideColumns,
              }}
              shuttle2={{
                heading: "Visible columns",
                items: shownCols,
                handleAcceptTransfer: showColumns,
              }}
            />
          </div>
        </ContentView>
      </ModalWrapper>
    </div>
  );
};

export default useColumnSelectAction;
