//https://github.com/tannerlinsley/react-table/

import React, { useEffect } from "react";
import { faFilter as falFilter } from "@faharmony/icons";
import { faFilter as fasFilter } from "@faharmony/icons/solid";
import { ButtonPopover, Button } from "@faharmony/components";

import { TableChildrenProps } from "../types";
import { commonButtonOptions } from "./common";
import { useExportAction } from "./useExportAction";
import { rowActions } from "./rowActions";
import { useColumnSelectAction } from "./useColumnSelectAction";

export const useTableActions = <T extends object>({
  tableInstance,
  tableProps,
}: TableChildrenProps<T>) => {
  const {
    enableFiltering = false,
    tableActions = [],
    exportTable,
    enableColumnVisibilitySelector = false,
  } = tableProps;
  const { setAllFilters } = tableInstance;
  const [enableFilter, setFilter] = React.useState(false);
  const exportAction = useExportAction({ tableInstance, tableProps });
  const columnSelectAction = useColumnSelectAction({
    tableInstance,
    tableProps,
  });

  // Reset all filters
  useEffect(() => {
    setAllFilters([]);
  }, [enableFilter]);

  // Initiate all actions with Row actions.
  const actions = [...rowActions({ tableInstance, tableProps })];

  // Add custom table actions
  if (tableActions && Array.isArray(tableActions))
    tableActions.map((action) =>
      actions.push(
        <ButtonPopover {...action} {...commonButtonOptions} key={action.id} />
      )
    );

  // Add default table actions

  // Export button
  if (exportTable) actions.push(exportAction);

  // Column Select button
  if (enableColumnVisibilitySelector) actions.push(columnSelectAction);

  // Filter Button
  if (enableFiltering)
    actions.push(
      <Button
        key="Filter"
        icon={enableFilter ? fasFilter : falFilter}
        tooltip={"Filter"}
        onClick={() => setFilter((prev: boolean) => !prev)}
        {...commonButtonOptions}
      />
    );

  return { actions, enableFilter };
};

export default useTableActions;
