import React from "react";
import { faFileCsv, faShare } from "@faharmony/icons";
import { ButtonPopover } from "@faharmony/components";

import { TableChildrenProps } from "../types";
import { commonButtonOptions } from "./common";

export const useExportAction = <T extends object>({
  tableInstance,
  tableProps,
}: TableChildrenProps<T>) => {
  const { exportTable = false, tableHeading } = tableProps;
  const { exportData } = tableInstance;
  const exportCSV =
    exportTable === true ||
    (Array.isArray(exportTable) && exportTable.indexOf("csv") !== -1);
  // const exportPDF = exportTable === true || (Array.isArray(exportTable) && exportTable.indexOf('pdf') !== -1);
  // const exportXLS = exportTable === true || (Array.isArray(exportTable) && exportTable.indexOf('excel') !== -1);

  const exportActions = [];
  if (exportCSV)
    exportActions.push({
      id: "csv",
      label: "Download CSV",
      onClick: () => exportData({ type: "csv", fileName: tableHeading }),
      icon: faFileCsv,
    });

  return (
    <div key={"exportAction"}>
      <ButtonPopover
        icon={faShare}
        tooltip={"Export"}
        {...commonButtonOptions}
        popoverMenuItems={exportActions}
      />
    </div>
  );
};

export default useExportAction;
