import React from "react";
import { uuid } from "@faharmony/helpers";
import { faCaretRight, faCaretDown } from "@faharmony/icons/solid";
import { ColumnInstance } from "react-table";
import {
  faSortAlt,
  faSortAmountDown,
  faSortAmountUpAlt,
} from "@faharmony/icons";
import { theme, Grid } from "@faharmony/theme";
import { Icon } from "@faharmony/components";
import { TableChildrenProps } from "../types";

// enableSorting ? column.getSortByToggleProps() : {}
interface TableHeadProps<T extends object> extends TableChildrenProps<T> {
  enableFilter: boolean;
}

const handleSort = <T extends object>(column: ColumnInstance<T>) => {
  if (column.isSorted) {
    if (column.isSortedDesc) column.clearSortBy();
    else column.toggleSortBy(true, false);
  } else column.toggleSortBy(false, false);
};

export const TableHead = <T extends object>({
  tableInstance,
  tableProps,
  enableFilter,
}: TableHeadProps<T>) => {
  const {
    headerGroups,
    toggleAllRowsExpanded,
    isAllRowsExpanded,
  } = tableInstance;
  const {
    enableSorting = false,
    enableMultipleRowSelect,
    data,
    renderRowSubComponent,
  } = tableProps;

  const enableExpand =
    data.filter((row: any) => (row.subRows ? true : false)).length > 0 ||
    renderRowSubComponent;

  return (
    <thead>
      {headerGroups.map((headerGroup) => (
        <React.Fragment key={uuid()}>
          <tr {...headerGroup.getHeaderGroupProps()}>
            {headerGroup.headers.map((column, index) => {
              const sortColumn = enableSorting && !column.disableSortBy;
              return (
                <th
                  {...column.getHeaderProps()}
                  title=""
                  style={{
                    width: column.width !== 150 ? `${column.width}px` : "auto",
                    minWidth: column.minWidth + "px",
                    maxWidth: column.maxWidth + "px",
                    textAlign:
                      index === (enableMultipleRowSelect ? 1 : 0)
                        ? undefined
                        : column.align,
                  }}
                >
                  <Grid
                    gridTemplate="auto / 1fr max-content"
                    className={column.className}
                  >
                    <div className="h5">
                      {enableExpand &&
                      ((enableMultipleRowSelect && index === 1) ||
                        (!enableMultipleRowSelect && index === 0)) ? (
                        // Row which can expand
                        <Grid gridTemplate="1fr / max-content 1fr">
                          <div
                            style={{ cursor: "pointer" }}
                            onClick={(e) => {
                              e.stopPropagation();
                              toggleAllRowsExpanded();
                            }}
                          >
                            <Icon
                              icon={
                                isAllRowsExpanded ? faCaretDown : faCaretRight
                              }
                            />
                          </div>
                          {column.render("Header")}
                        </Grid>
                      ) : (
                        column.render("Header")
                      )}
                    </div>

                    {sortColumn && (
                      <div
                        className="table-header-sort"
                        title="Sort column"
                        onClick={() => handleSort<T>(column)}
                      >
                        {column.isSorted ? (
                          <Icon
                            icon={
                              column.isSortedDesc
                                ? faSortAmountDown
                                : faSortAmountUpAlt
                            }
                            color={theme.color.text.primary}
                          />
                        ) : (
                          <Icon icon={faSortAlt} />
                        )}
                      </div>
                    )}
                  </Grid>
                </th>
              );
            })}
          </tr>
          {enableFilter ? (
            <tr className="filterRow" key={"filterRow"}>
              {headerGroup.headers.map((column) => (
                <th key={uuid()}>
                  {column.canFilter ? column.render("Filter") : null}
                </th>
              ))}
            </tr>
          ) : null}
        </React.Fragment>
      ))}
    </thead>
  );
};

export default TableHead;
